@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@300;400;500;700&display=swap');
*{
  font-family: 'Noto Sans JP', sans-serif;
}
.App {
  
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.moons-wrapper #footer-mobile{
  position: fixed;  
  text-align: center;
  bottom: 0;  
  width: 100%;
  border-top: 1px solid #e4e4e4;
  padding-top: 5px;
  background: #fff;
}

.moons-wrapper #footer-mobile .active{
  color:#0752f8;
  font-weight: bold;
}

.moons-wrapper #footer-mobile a{
text-decoration: none;
color:#252525;
}
.moons-wrapper #footer-mobile i{
  display: block;
}

